import React, { useState } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Topline from './Topline';
import Platform from './Platform';
import Market from './Market';
import Products from './Products';
import Operations from './Operations';

import './executiveCockpit.scss';



const ExecutiveCockpit = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('topline')
  }

  const breadcrumbs = [
    {
      path: path === '/executive-cockpit/topline' ? null : '/executive-cockpit/topline',
      label: 'Executive cockpit'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/executive-cockpit/topline',
      label: 'Topline'
    },
    {
      path: '/executive-cockpit/platform',
      label: 'Platform'
    },
    {
      path: '/executive-cockpit/market',
      label: 'Market'
    },
    {
      path: '/executive-cockpit/customer',
      label: 'Customer'
    },
    {
      path: '/executive-cockpit/products',
      label: 'Products'
    },
    {
      path: '/executive-cockpit/operations',
      label: 'Operations'
    }
  ]

  return (
    <div className="executiveCockpit-page">
      <PageHeader 
        title="EXECUTIVE COCKPIT" 
        color="dark-orange" 
        innerPage={innerPage} 
        breadcrumbs={breadcrumbs}
        previousPage="/"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'topline' && <Topline />}
        {currentPath === 'platform' && <Platform/>}
        {currentPath === 'market' && <Market/>}
        {currentPath === 'products' && <Products/>}
        {currentPath === 'operations' && <Operations/>}
      </div>
    </div>
  );
}

export default ExecutiveCockpit;
