import React, { useRef, useEffect }  from 'react';
import { Button } from 'react-bootstrap';
import './filter.scss';
import { motion, AnimatePresence } from 'framer-motion';

const animation = {
  hidden: {
    opacity: 0,
    y: '-130px',
  },
  visible: {
    opacity: 1,
    y: '0',
  },
  exit: {
    opacity: 0,
    transition: { duration: .1 }
  }
}


const FilterModal = ({ title, children, className, showModal, handleCloseModal, setShowModal }) => {
  const wrapperRef = useRef(null);

  const useOutsideAlerter = (ref) => {
    useEffect(() => {
      const handleClickOutside = (event) => {
        if (ref.current && !ref.current.contains(event.target)) {
          setShowModal(false);
        }
      }
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  }

  useOutsideAlerter(wrapperRef);

  return (
    <>
    <AnimatePresence exitBeforeEnter>
      {showModal  
        ? <motion.div ref={wrapperRef} className={`custom-modal filter-modal ${className ? className : ''}`}
          variants={animation}
          initial='hidden'
          animate='visible'
          exit='exit'
          >
            {/* <div className="modal-header">
              All {title} Filters
            </div> */}
            <div className="modal-body">
              {children}
            </div>
            <div className="modal-footer">
              <Button variant="link" onClick={handleCloseModal}>
                Cancel
              </Button>
              <Button variant="primary" onClick={handleCloseModal}>
                Apply
              </Button>
            </div>
          </motion.div>
        : null
      }
      </AnimatePresence>
    </>
  );
}

export default FilterModal;
