// Utils
import React from 'react';
// Components
import { Dropdown } from 'react-bootstrap';
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import filterFactory from 'react-bootstrap-table2-filter';
import paginationFactory, { PaginationProvider, PaginationListStandalone, SizePerPageDropdownStandalone } from 'react-bootstrap-table2-paginator';
import { CgSortAz, CgSortZa } from "react-icons/cg";
// Stylesheets
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import './customTable.scss';

const CustomTable = props => {
  const { data, columns, tableTitle, addSearch, addSort, addPaginate, addCustomBody, tableName, rowClasses, selectRow, defaultSorted } = props;
  const { SearchBar } = Search;

  const customCaret = (order, column) => {
    if (!order) return null;
    else if (order === 'asc') return (<CgSortAz className="sort-icon"/>);
    else if (order === 'desc') return (<CgSortZa className="sort-icon"/>);
    return null;
  }

  const emptyState = () => {
    return <p className="mb-0">Table is Empty</p>;
  }

  const sizePerPageRenderer = ({ options, currSizePerPage, onSizePerPageChange }) => {
    return (
      <Dropdown>
        <span className="mr-2">Sort</span>
        <Dropdown.Toggle id="size-per-page" variant="light">
          {data.length === Number(currSizePerPage) ? 'All' : currSizePerPage}
        </Dropdown.Toggle>
        <Dropdown.Menu>
          {options.map(option => (
            <Dropdown.Item key={option.text} type="button" onClick={() => onSizePerPageChange(option.page)}>
              {option.text}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    );
  }

  const formatColumns = columns.map(item => {
    if (item.sort === true) {
      return {
        ...item,
        sortCaret: customCaret
      };
    }
    return item;
  })

  const options = {
    sizePerPageRenderer,
    custom: true,
    paginationSize: 1,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: true,
    firstPageText: '<<',
    prePageText: '<',
    nextPageText: '>',
    lastPageText: '>>',
    nextPageTitle: 'Next page',
    prePageTitle: 'Previous page',
    firstPageTitle: 'First page',
    lastPageTitle: 'Last page',
    showTotal: true,
    totalSize: data.length,
    sizePerPageList: [{
      text: '20',
      value: 20
    }, {
      text: '30',
      value: 30
    }],
  }

  const contentTable = ({ paginationProps, paginationTableProps }) => {
    return (
      <ToolkitProvider
        keyField="_id"
        data={data}
        columns={formatColumns}
        search={ { searchFormatted: true } }
        >
        {toolkitprops => (
          <div className="table-container">
            <div className="table-header">
              <div className="page-header">
                {tableTitle && <h2 className="table-title">{tableTitle}</h2>}
                {addSearch && <SearchBar {...toolkitprops.searchProps} />}
                <div className="right">
                  {props.children}
                  {addSort && <SizePerPageDropdownStandalone {...paginationProps} />}
                </div>
              </div>
            </div>
            <div className="table-body">
              {addCustomBody && <>{addCustomBody}</>}
              <BootstrapTable
                classes={tableName ? tableName : null}
                bordered={false}
                hover
                defaultSorted={defaultSorted}
                rowClasses={ rowClasses ? rowClasses : null }
                noDataIndication={emptyState}
                filter={filterFactory()}
                selectRow={ selectRow }
                {...toolkitprops.baseProps}
                {...paginationTableProps}
              />
            </div>
            {addPaginate && <div className="table-footer">
              <PaginationListStandalone {...paginationProps} />
            </div>}
          </div>
        )}
      </ToolkitProvider>
    );
  }

  return (
    <div className={`custom-scroll ${props.className ? props.className : null}`}>
      <PaginationProvider pagination={paginationFactory(options)}>
        {contentTable}
      </PaginationProvider>
    </div>
  );
}

export default CustomTable;