import React, { useState } from 'react';
import { Dropdown } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi';
import './customDropdown.scss';


const ResponsiveBasicDD = ({ options, wide, narrow, className, id, selection }) => {
  const initialValue = options ? options[0].text : 'try adding something to click to, use "option" props.'
  const [value, setValue] = useState(initialValue);
  const [text, setText] = useState(initialValue);
  const handleSelect = (key, text) => {
    setValue(key)
    setText(text)
    selection(key);
  }
  return (
    <>
      <ul className={`responsive-basic-dd wide-list ${wide ? 'visible' : 'hidden'} ${className ? className : ''}`}>
        {options
          ? <>
            {options.map(option => (
              <li key={option.key}
                onClick={() => handleSelect(option.key)}
                className={`${option.key === value ? 'active' : ''}`}
              >
                {option.text}
              </li>
            ))}
          </>
          : <li>{initialValue}</li>
        }
      </ul>

      <Dropdown className={`responsive-basic-dd narrow-list ${narrow ? 'visible' : 'hidden'} ${className ? className : ''}`}>
        <Dropdown.Toggle id={`responsive_basic_dd-${id}`} variant='light'>
          {text} <BiChevronDown viewBox="6 8 12 12" />
        </Dropdown.Toggle>

        <Dropdown.Menu>
          {options && options.map(option => (
            <Dropdown.Item
              key={option.key}
              eventKey={option.key}
              onClick={() => handleSelect(option.key, option.text)}
            >
              {option.text}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    </>
  );
}

export default ResponsiveBasicDD;
