import React from 'react';

import './chart.scss';


const StackedColumnBar = (props) => {
  var totalValue = props.data.reduce((a, b) => +a + +b.value, 0);

  for (const key in props.data) {
    const element = props.data[key];
    element.percent = Math.floor((element.value / totalValue) * 100);
  }

  return (
    <>
      <div className="stacked-column-bar">
        {props.data &&
          props.data.map((d, i) => {
            return (
              <div key={i} className="stacked-bar" style={{ background: d.color, width: `${d.percent}%` }} />
            );
          })}
      </div>
    </>
  );
}



export default StackedColumnBar;
