import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import './kpi.scss';

const Kpi = (props) => {
  const { data } = props;
  return (
    <>
      <div className="total-revenue">{data.revenue.value.slice(0, 1)}<span>{data.revenue.value.slice(1, -1)}</span>{data.revenue.value.slice(-1)}</div>
      {Object.keys(data).map((key, i) => {
        return (
          <div key={i}>
            {data[key].value.slice(0, 1) === '$' ? null :
              <div className={data[Object.keys(data)[Object.keys(data).length - 1]].label === data[key].label ? 'kpi-item' : 'kpi-item border-bottom'}>
                <div className="title">{data[key].label}</div>
                <div className="div-table">
                  <div className="tr">
                    <div className="td val"><span> {data[key].value.slice(0, -1)}</span>{data[key].value.slice(-1)}</div>
                    <div className="td middle"><span className="indicator indicator-down" > <ImArrowDownRight2 /> {data[key].pp_value}</span></div>
                    <div className="td middle"><span className="period" >vs {data[key].quarter_period}</span></div>
                  </div>
                </div>
              </div>
            }
          </div>
        )
      })}
    </>
  );
}
export default Kpi;