import React from 'react';
import { useHistory, Link } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap';
import { ReactComponent as Logo } from '../../assets/images/orbis-logo-dark.svg';
import Video from '../../assets/videos/main-visual.mp4';
import { BiChevronDown } from 'react-icons/bi';

import './login.scss';

const Login = () => {
    let history = useHistory();

    const handleSubmit = () => {
        history.push("/sso-equinix-com");
    }
    return (
        <div className="login-page">
            <section className="section-1">
                <div className="video-container">
                    <video autoPlay muted loop className="video" id="mainVisual">
                        <source src={Video} type="video/mp4" />
                        Your browser does not support HTML5 video.
                    </video>
                </div>
                <div className="section-wrapper">
                    <div className="login-block">
                        <div className="login-container">
                            <div className="logo">
                                <Logo width="277px"/>
                            </div>
                            <p>Welcome back! Please login to your account.</p>
                            <Form onSubmit={handleSubmit}>
                                <Form.Group controlId="username">
                                    <Form.Control type="text" placeholder="Username" name="username" required />
                                </Form.Group>
                                <div className="btn-block">
                                    <Button type="submit" className="btn-login">Login</Button>
                                    <Button variant="light" className="btn-request">Request Access</Button>
                                </div>
                                <Link to="/login" className="terms">Term of use. Privacy policy</Link>
                            </Form>
                        </div>
                        <Button variant="link" className="btn-scroll"><BiChevronDown viewBox="7 8 10 10" className="icon"/> Scroll Down to Learn More</Button>
                    </div>
                </div>
            </section>
        </div>   
    );
};

export default Login;
