import React from 'react';
import './loader.scss'

const Loader = ({ withContainer, width }) => {
  return (
    <>
      {withContainer 
        ? <div className="loader">
            <svg version="1.1" id="L3"
              className="loadingSvg"
              x="0px" y="0px"
              viewBox="0 0 100 100" style={{ width: width ? width : '100px'}}>
              <circle fill="none" stroke="#CD503C" strokeWidth="4" cx="50" cy="50" r="44" style={{ opacity: 0.5 }} />
              <circle fill="#CD503C" stroke="#fff" strokeWidth="3" cx="8" cy="54" r="6" >
                <animateTransform
                  attributeName="transform"
                  dur="2s"
                  type="rotate"
                  from="0 50 48"
                  to="360 50 52"
                  repeatCount="indefinite" />
              </circle>
            </svg>
          </div>
        : <svg version="1.1" id="L3"
          className="loadingSvg"
          x="0px" y="0px"
          viewBox="0 0 100 100" style={{ width: width ? width : '100px'}}>
          <circle fill="none" stroke="#CD503C" strokeWidth="4" cx="50" cy="50" r="44" style={{ opacity: 0.5 }} />
          <circle fill="#CD503C" stroke="#fff" strokeWidth="3" cx="8" cy="54" r="6" >
            <animateTransform
              attributeName="transform"
              dur="2s"
              type="rotate"
              from="0 50 48"
              to="360 50 52"
              repeatCount="indefinite" />
          </circle>
        </svg>
      }
    </>
  );
}

export default Loader;