import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import sampleFlagImage from '../../assets/images/sample-flag.png';
import MetroModal from './MetroModal';
//Charts
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import Legend from '../../components/legend/Legend';
import LineChart from '../../components/chart/LineChart';
import CustomerModal from './CustomerModal';


const CustomerContent = (props) => {
  const { customerData, modalShow, modalData, setModalShow, setModalData, setReferenceID } = props;
  const top_ten_metros_by_mrr_stacked_column = customerData.top_ten_metros_by_mrr_stacked_column;
  const top_ten_metros_by_mrr_line = customerData.top_ten_metros_by_mrr_line;
  const deployment_map = customerData.deployment_map;
  const customer_ranking = customerData.customer_ranking;
  const by_rank_id = customer_ranking.by_rank_id;
  return (
    <Container fluid>
      <CustomerModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
      />
      <Row className="contentDivider">
        <Col md={7}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Top 10 Customers by MRR</Card.Title>
              <Legend legend={top_ten_metros_by_mrr_stacked_column.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="top_ten_metros_by_mrr_stacked_column"
                  isOneHundredPercent={true}
                  isPercentage={true}
                  hiddenValueAxis={true}
                  data={top_ten_metros_by_mrr_stacked_column.data}
                  legend={top_ten_metros_by_mrr_stacked_column.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={5}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Top 10 Customers by MRR</Card.Title>
              <Legend legend={top_ten_metros_by_mrr_line.legend} />
              <div style={{ height: "250px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={top_ten_metros_by_mrr_line.data}
                  legend={top_ten_metros_by_mrr_line.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={deployment_map.legend} />
              <div style={{ height: "420px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"customer_revenue_map_chart"}
                  largerWidth={true}
                  data={deployment_map.data}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>customer ranking</Card.Title>
              <Legend legend={customer_ranking.by_rank.legend} />
              <div style={{ paddingTop: '2.5rem' }}>
                <Table responsive>
                  <thead>
                    <tr>
                      <th className="header-align-center">RANK</th>
                      <th className="header-align-left">customer</th>
                      <th className="header-align-center">vertical</th>
                      <th className="header-align-right">mrr</th>
                      <th className="header-align-right">MIX%</th>
                      <th></th>
                      <th className="header-align-right">Y/Y GROWTH%</th>
                      <th className="header-align-right">Y-Y $ GROWTH</th>
                      <th className="header-align-right">%total $growth</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      customer_ranking.by_rank.data && customer_ranking.by_rank.data.length > 0 ?
                        customer_ranking.by_rank.data.map((data, i) => {
                          var modalDataByRank;
                          for (const key in by_rank_id) {
                            const element = by_rank_id[key];
                            if (element._id === data._id) {
                              modalDataByRank = element
                            }
                          }
                          if (data._id <= 7) {
                            return (
                              <tr key={i} id={`metro_ranking_row_${data._id}`}
                                className="height1"
                                onDoubleClick={() => setReferenceID(null)}
                                onClick={() => {
                                  setModalShow(true);
                                  setReferenceID(data._id);
                                  setModalData(modalDataByRank);
                                }}
                                className={data._id === 5 ? 'border-head' : null}>
                                <td className="body-rank-column">{data.rank}</td>
                                <td className="body-rank-column">{data.customer}</td>
                                <td className="body-values-column">{data.vertical}</td>
                                <td className="body-values-column">{data.mrr}</td>
                                <td className="body-values-column">{data.mrr_mix_percent}</td>
                                <td className="sparkline-td">
                                  <SparklineCustom />
                                </td>
                                <td className="body-values-column">
                                  <span className="indicator indicator-up " style={{ top: "70.7%" }}>
                                    <ImArrowUpRight2 />
                                    {data.y_by_y_growth_percent}
                                  </span>
                                </td>
                                <td className="body-values-column"> {data.y_to_y_usd_growth}</td>
                                <td className="body-values-column">{data.percent_total_usd_growth}</td>
                              </tr>);
                          } else {
                            return null;
                          }
                        })
                        : null
                    }
                  </tbody>
                </Table>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>

  );
};

export default CustomerContent;