import React from 'react';
import { Dropdown } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';

const BasicDD = ({ options, className, id, title }) => {
  return (
    <>
      <Dropdown className={`basic-dd narrow-list ${className ? className : ''}`}>
        <Dropdown.Toggle id={`basic_dd-${id}`} variant="light">
          {title} <BiChevronDown viewBox="6 8 12 12" />
        </Dropdown.Toggle>

        <Dropdown.Menu>
          {options && options.map(option => (
            <Dropdown.Item
              key={option.key}
              eventKey={option.key}
            >
              {option.text}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    </>
  );
}

export default BasicDD;
