import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

// filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';

import { API_BASE_URL } from '../../constants/config';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import Loader from '../../components/loader/Loader';
import Kpi from '../..//components/kpi/Kpi';
import OverviewContent from './OverviewContent';


const Overview = () => {
    const [overviewData, setData] = useState(null);
    const [isLoading, setLoadingStatus] = useState(true);

    const fetchData = async () => {
        setLoadingStatus(true);

        await axios({
            method: 'GET',
            url: `${API_BASE_URL}/customer-revenue/overview.json`
        }).then(result => {
            setData(result.data);
            setLoadingStatus(false);
        }).catch(error => {
            console.log(error);
            setLoadingStatus(false);
        });;
    };

    useEffect(() => {
        let isMounted = true;

        if (isMounted) {
            fetchData();
        }

        return () => {
            isMounted = false;
        };
    }, []);

    const segment_details = overviewData ? overviewData.segment_details : [];
    const overall = overviewData ? overviewData.overall : [];

    const cardRef = useRef();
    const { width } = useWidowResize(cardRef);

    if (isLoading) return <div ref={cardRef}><Loader /></div>;

    return (
        <Container className="overview" fluid ref={cardRef}>
            <FilterContainer title="Topline">
                <CheckboxDD
                    title="Region"
                    id="measeure"
                    options={optionRegion}
                />
                <SearchCheckboxDD
                    title="Metro"
                    id="metro"
                    options={optionMetro}
                />
                <RadioDD
                    title="Measure"
                    id="measeure"
                    options={optionMeasure}
                />
                <Separator height='39px' />
                <ResponsiveRadioDD
                    title="Range by"
                    id="range2"
                    wide={width >= 1394 ? true : false}
                    narrow={width < 1394 ? true : false}
                    options={optionRange}
                />
                <Separator height='39px' />
                <ResponsiveRadioDD
                    title="Group by"
                    id="group"
                    addTitleOnWide
                    wide={width >= 1516 ? true : false}
                    narrow={width < 1516 ? true : false}
                    options={optionGroup}
                />
            </FilterContainer>
            <OverviewContent
                segment_details={segment_details}
                overall={overall}
                overviewData={overviewData} />
        </Container >
    );
};

export default Overview;
