import React from 'react';
import { NavLink, Link } from "react-router-dom";

/** SVG image and icons */
import { ReactComponent as BrandLogo } from '../../assets/images/orbis-logo-light.svg';
import { ReactComponent as Hamburger } from '../../assets/icons/hamburger.svg';
import { ReactComponent as Plane } from '../../assets/icons/plane.svg';
import { ReactComponent as Shutter } from '../../assets/icons/shutter.svg';
import { ReactComponent as Flare } from '../../assets/icons/flare.svg';
import { ReactComponent as Add } from '../../assets/icons/add.svg';
import { ReactComponent as Info } from '../../assets/icons/info.svg';
import { ReactComponent as Settings } from '../../assets/icons/settings.svg';
import { ReactComponent as Logout } from '../../assets/icons/logout.svg';

import './sidebar.scss';

const Sidebar = (props) => {
	const { open, toggleOpen } = props;

	const menuTop = [
		{
			path: "/executive-cockpit/topline",
			icon: <Plane className="icon" />,
			label: "Executive Cockpit",
		},
		{
			path: "/snapshot-builder/reports",
			icon: <Shutter className="icon" />,
			label: "Snapshot Builder",
		},
		{
			path: "/customer-navigator",
			icon: <Flare className="icon" />,
			label: "Customer Navigator",
		}
	];

	return (
		<div className={`sidebar ${open === true ? 'open' : 'closed'}`}>
			<nav className={`menu ${open === true ? 'open' : 'closed'}`}>
				<div className="header">
					<Link to="/" className="logo">
						<BrandLogo />
					</Link>
					<div className="hamburger" onClick={() => toggleOpen()}>
						<Hamburger />
					</div>
				</div>
				<div className="content">
					<ul className="top">
						{menuTop && menuTop.map((item, index) => {
							return (
								<li key={index} className="item">
									<NavLink exact to={item.path}>
										{item.icon}
										<span>{item.label}</span>
									</NavLink>
								</li>
							)
						})}
						<li className="item">
							<a href="/">
								<Add className="icon" />
								<span>Add Products</span>
							</a>
						</li>
					</ul>

					<ul className="bottom">
						<li className="item">
							<NavLink to="/help-center">
								<Info className="icon" />
								<span>Help Center</span>
							</NavLink>
						</li>
						<li className="item">
							<NavLink to="/settings">
								<Settings className="icon" />
								<span>Settings</span>
							</NavLink>
						</li>
						<li className="item">
							<NavLink to="/login">
								<Logout className="icon" />
								<span>Logout</span>
							</NavLink>
						</li>
					</ul>
				</div>
			</nav>
		</div>
	);
}

export default Sidebar;