import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';

const OverviewContent = (props) => {
  const { segment_details, overall, overviewData } = props;
  
  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>total revenue</Card.Title>
              <Kpi data={overall} />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>total revenue by revenue type</Card.Title>
              <Legend legend={overviewData.total_revenue_by_revenue_type.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart 
                    id="total_revenue_by_revenue_type_stacked_column_chart" 
                    hiddenValueAxis={false}
                    isOneHundredPercent={false}
                    isPercentage={false}
                    data={overviewData.total_revenue_by_revenue_type.data} 
                    legend={overviewData.total_revenue_by_revenue_type.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>segment revenue mix</Card.Title>
              <Legend legend={overviewData.segment_revenue_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"total_revenue_overview_donut_chart"}
                  totalTitle={"TOTAL REVENUE"}
                  total={overviewData.segment_revenue_mix.total_revenue}
                  data={overviewData.segment_revenue_mix.data}
                  legend={overviewData.segment_revenue_mix.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <Table responsive>
                <thead>
                  <tr>
                    <th>segment</th>
                    <th className="text-right">revenue</th>
                    <th className="text-center">mrr mix%</th>
                    <th>y/y growth%</th>
                    <th>y-y $ growth</th>
                    <th>q/q growth%</th>
                    <th>q-q $ growth</th>
                    <th>%budget</th>
                    <th>%mpq</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    segment_details && segment_details.length > 0 ?
                      segment_details.map((data, i) => {
                        return <tr key={i}>
                          <td>{data.segment}</td>
                          <td className="text-right">{data.revenue}</td>
                          <td className="text-center">{data.mix_percent}</td>
                          <td>
                            <span className="indicator indicator-up"> <ImArrowUpRight2 /> {data.y_by_y_growth_percent}%</span>
                          </td>
                          <td>{data.y_to_y_growth_usd}</td>
                          <td>
                            <span className="indicator indicator-down"> <ImArrowDownRight2 /> {data.q_by_q_growth_percent}%</span>
                          </td>
                          <td> {data.q_to_q_growth_usd}</td>
                          <td>{data.percent_budget}</td>
                          <td>{data.percent_mpq}</td>
                        </tr>
                      }) : null
                  }
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={overviewData.segment_trends.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                    id={"total_revenue_overview_line_chart"}
                    data={overviewData.segment_trends.data}
                    legend={overviewData.segment_trends.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default OverviewContent;