import React, { useRef, useState, useEffect } from 'react';
import './executiveCockpit.scss';
import axios from 'axios';
// filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { comments } from '../../constants/comments';
import { optionMeasure, optionRange, optionGroup, optionRegion, optionMetro, optionHyperscaler } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
// comment components
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import Content from './Content';
// constant
import { API_BASE_URL } from '../../constants/config';

const Topline = (props) => {
  // Axios call
  const [overviewData, setOverviewData] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  
  const fetchPretty = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/total-revenue/overview.json`,
    }).then(result => {
      setOverviewData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
  }, []);

  const segment_details = overviewData ? overviewData.segment_details : [];

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showModal, setShowModal] = useState(false);
  const handleCloseModal = () => {
    setShowModal(false);
  }
  const handleShowModal = () => {
    setShowModal(true)
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const handleCloseComment = () => {
    setShowComment(false);
  }
  const handleShowComment = () => {
    setShowComment(true)
  };

  if (isLoading) return <div className="w-100" ref={cardRef}><Loader /></div>;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          handleShowModal={handleShowModal}
          handleShowComment={handleShowComment}
        >
          <div className="filters">
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
            />
            <Separator height='39px' />
            {/* <ResponsiveRadioDD
                title="Group by"
                id="group"
                addTitleOnWide
                wide={width >= 1516 ? true : false}
                narrow={width < 1516 ? true : false}
                options={optionGroup}
              />
              <Separator height='39px' /> */}
            <ResponsiveRadioDD
              id="hyperscaler"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionHyperscaler}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowModal={setShowModal}
            showModal={showModal}
            handleCloseModal={handleCloseModal}
          >
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
              visibleInModal
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              visibleInModal
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              options={optionGroup}
              visibleInModal
            />
          </FilterModal>
        </FilterContainer>
        <Content 
          data={segment_details}
        />
      </div>
      <Comment
        data={comments}
        handleCloseComment={handleCloseComment}
        showComment={showComment}
      />
    </>
  )
};

export default Topline;

