import React, { useState } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Reports from './Reports';
import CreateNew from './CreateNew';

import './snapshot.scss';



const Snapshot = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Reports')
  }

  const breadcrumbs = [
    {
        path: path === '/snapshot-builder/reports' ? null : '/snapshot-builder/reports',
        label: 'SNAPSHOT REPORT BUILDER'
    },
    {
        label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/snapshot-builder/reports',
      label: 'Reports'
    },
    {
      path: '/snapshot-builder/create-new',
      label: 'Create New'
    },
  ]

  return (
    <div className="snapshot-page">
      <PageHeader 
        title="SNAPSHOT INSIGHTS BUILDER" 
        color="turquoise" 
        innerPage={innerPage} 
        breadcrumbs={breadcrumbs}
        previousPage="/"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'reports' && <Reports/>}
        {currentPath === 'create-new' && <CreateNew/>}
      </div>
    </div>
  );
}

export default Snapshot;
