import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow"
import numbro from 'numbro';
import _ from 'lodash';

const MapChart = (props) => {

  useLayoutEffect(() => {
    var chart = am4core.create(props.id, am4maps.MapChart);
    var currentSeries;
    var mapData;

    chart.width = am4core.percent(100);
    chart.height = am4core.percent(45);
    chart.homeZoomLevel =3.2;
    chart.homeGeoPoint = {
      latitude: 62,
      longitude: 20
    };

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 60;
    chart.paddingBottom = 0;

    chart.geodata = am4geodata_worldLow;

    chart.projection = new am4maps.projections.Miller();
    var polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

    polygonSeries.useGeodata = true;

    var polygonTemplate = polygonSeries.mapPolygons.template;
    polygonTemplate.fill = am4core.color("#DBE2ED");

    // Remove Antarctica Continent from map
    polygonSeries.exclude = ["AQ"];

    let createSeries = (columnType, currentKey, minRadius, maxRadius) => {

      if (columnType === "region") {
        mapData = _.chain(props.data).groupBy(columnType).map(data => ({
          "region": data[0].region,
          "region_short": data[0].region_short,
          "type": "region",
          "latitude": parseFloat(data[0].region_lat),
          "longitude": parseFloat(data[0].region_long),
          "color": data[0].region_short === "AMER" ? "#DC7431" : data[0].region_short === "APAC" ? "#CD503C" : "#F1D073",
          "revenue": numbro(_.sumBy(data, item => parseInt(item.revenue.replace(/\D/g, "")))).formatCurrency({ thousandSeparated: true })
        })).value();
      } else if (columnType === "country") {
        let data = _.chain(props.data).groupBy(['region', currentKey]).value().true

        mapData = _.chain(data).groupBy(columnType).map(data => ({
          "region": data[0].region,
          "region_short": data[0].region_short,
          "country": data[0].country,
          "country_short": data[0].country_short,
          "type": "country",
          "latitude": parseFloat(data[0].country_lat),
          "longitude": parseFloat(data[0].country_long),
          "color": data[0].region_short === "AMER" ? "#DC7431" : data[0].region_short === "APAC" ? "#CD503C" : "#F1D073",
          "revenue": numbro(_.sumBy(data, item => parseInt(item.revenue.replace(/\D/g, "")))).formatCurrency({ thousandSeparated: true })
        })).value();
      } else if (columnType === "metro") {
        let data = _.chain(props.data).groupBy(['country', currentKey]).value().true

        mapData = _.chain(data).groupBy(columnType).map(data => ({
          "region": data[0].region,
          "region_short": data[0].region_short,
          "country": data[0].country,
          "country_short": data[0].country_short,
          "metro": data[0].metro,
          "metro_short": data[0].metro_short,
          "type": "country",
          "latitude": parseFloat(data[0].metro_lat),
          "longitude": parseFloat(data[0].metro_long),
          "color": data[0].region_short === "AMER" ? "#DC7431" : data[0].region_short === "APAC" ? "#CD503C" : "#F1D073",
          "revenue": numbro(_.sumBy(data, item => parseInt(item.revenue.replace(/\D/g, "")))).formatCurrency({ thousandSeparated: true })
        })).value();
      } else {

      }

      var zoomOut = chart.tooltipContainer.createChild(am4core.ZoomOutButton);
      // zoomOut.width = am4core.percent(100);
      // zoomOut.height = am4core.percent(40);
      zoomOut.align = "right";
      zoomOut.margin(20, 20, 20, 20);
      zoomOut.events.on("hit", function () {
        currentSeries.dispose()
        chart.goHome();

        if (props.largerWidth === true) {
          createSeries('region', null, 50, 100);
        } else {
          createSeries('region', null, 40, 80);
        }

        zoomOut.hide()
      });
      zoomOut.icon = new am4core.Sprite();
      zoomOut.icon.fill = am4core.color('#ffffff')
      zoomOut.icon.path = "M16,8 L14,8 L14,16 L10,16 L10,10 L6,10 L6,16 L2,16 L2,8 L0,8 L8,0 L16,8 Z M16,8";

      var imageSeries = chart.series.push(new am4maps.MapImageSeries());

      var imageSeriesTemplate = imageSeries.mapImages.template;
      var circle = imageSeriesTemplate.createChild(am4core.Circle);

      circle.radius = 4;
      circle.fillOpacity = 0.8;
      circle.strokeWidth = 2;
      circle.nonScaling = true;
      circle.tooltipText = `{${columnType}}`;
      circle.propertyFields.stroke = "color";
      circle.propertyFields.fill = "color";
      circle.tooltipText = `[#fff]{${columnType}}: [#fff bold]\${value}[/]`;

      var label = imageSeriesTemplate.createChild(am4core.Label);
      label.text = columnType === "region" ? "{region_short}" : columnType === "country" ? "{country_short}" : "{metro_short}";
      label.fill = am4core.color("#fff");
      label.verticalCenter = "middle";
      label.horizontalCenter = "middle";
      label.nonScaling = true;

      imageSeries.heatRules.push({
        "target": circle,
        "property": "radius",
        "min": minRadius,
        "max": maxRadius,
        "dataField": "value"
      });
      imageSeriesTemplate.propertyFields.latitude = "latitude";
      imageSeriesTemplate.propertyFields.longitude = "longitude";
      imageSeries.dataFields.value = "revenue";

      imageSeries.data = mapData;
      currentSeries = imageSeries;

      currentSeries.mapImages.template.events.on("hit", function (ev) {
        var data = ev.target.dataItem.dataContext;
  
        if (data.type === "region") {
          currentSeries.dispose();

          if (props.largerWidth === true) {
            ev.target.series.chart.zoomToMapObject(ev.target, 8);
            createSeries("country", data.region, 20, 40);
          } else {
            ev.target.series.chart.zoomToMapObject(ev.target, 12);
            createSeries("country", data.region, 15, 30);
          }

          zoomOut.show();
        } else if (data.type === "country") {
          currentSeries.dispose();

          if (props.largerWidth === true) {
            ev.target.series.chart.zoomToMapObject(ev.target, 20);
            createSeries("metro", data.country, 15, 30);
          } else {
            ev.target.series.chart.zoomToMapObject(ev.target, 40);
            createSeries("metro", data.country, 10, 20);
          }
          
          zoomOut.show();
        } else {
          currentSeries.dispose()
          chart.goHome();

          if (props.largerWidth === true) {
            createSeries('region', null, 50, 100);
          } else {
            createSeries('region', null, 40, 80);
          }
          
        }
      });

    };

    if (props.largerWidth === true) {
      createSeries('region', null, 50, 100);
    } else {
      createSeries('region', null, 40, 80);
    }
  });

  return (
    <div id={props.id} className="map-chart"></div>
  );
};

export default MapChart;