import React from 'react';

import './chart.scss';


const SparklineCustom = () => {
  const dummyData = [
    {
      value: 30
    },
    {
      value: 80
    },
    {
      value: 30
    },
    {
      value: 85
    },
    {
      value: 85
    }
  ];

  return (
    <>
      <svg width="100%" height="auto" style={{ padding: '5px', overflow: 'visible' }} transform="scale(1,-1)">
        {dummyData.map((data, index) => {
          let nextObject = dummyData[index + 1];
          let nextObjectValue = nextObject ? nextObject.value : data.value;
          let xPosition = index * 25;
          let circleRadius = 3;
          return (
            <g key={index}>
              {nextObject &&
                <>
                  {index === 0
                    ? <circle r="3"
                      fill="#fff"
                      strokeWidth="1"
                      stroke={data.value > nextObjectValue ? '#FF0000' : data.value < nextObjectValue ? '#00B050' : '#FFC000'}
                      cx={`${xPosition}%`} cy={`${data.value}%`}>
                    </circle>
                    : null
                  }
                  <circle r={circleRadius}
                    fill="#fff"
                    strokeWidth="1"
                    stroke={data.value > nextObjectValue ? '#FF0000' : data.value < nextObjectValue ? '#00B050' : '#FFC000'}
                    cx={`${xPosition + 25}%`} cy={`${nextObjectValue}%`}>
                  </circle>
                  <line
                    x1={`${xPosition + circleRadius}%`} y1={`${data.value + circleRadius}%`}
                    x2={`${xPosition + 21}%`} y2={`${nextObjectValue}%`}
                    strokeWidth="1"
                    stroke={data.value > nextObjectValue ? '#FF0000' : data.value < nextObjectValue ? '#00B050' : '#FFC000'} />
                </>
              }
            </g>
          )
        })}
      </svg>
    </>
  );
}



export default SparklineCustom;
