import React from 'react';
import './legend.scss';

const Legend = (props) => {
    let legend = props.legend;

    return (
      <div className="legend-chart clearfix">
        <ul>
          {
            legend.sort((first, second) => (first._id - second._id)).map((legend, i) => {
              return legend ? 
                  <li key={i}>
                    <span className="legend-color" style={{ backgroundColor: legend.color }}>&nbsp;</span>
                    <span className="legend-name">{legend.name}</span>
                  </li>
                : null;
            })
          }
        </ul>
      </div>
    );
}

export default Legend;
