import React from 'react';
import { ButtonGroup, Button } from 'react-bootstrap';
import './filter.scss';

const FilterContainer = ({ children, className, handleShowModal, handleShowComment, showComment }) => {

  return (
    <div className={`filter-container ${className ? className : ''}`}>
      
      {children}

      <ButtonGroup className={`ml-auto ${showComment ? 'comment-open' : '' }`}>
        <Button variant="link" className="btn-all-filters" onClick={handleShowModal}>
          All Filters
        </Button>
          <Button variant="link" className="btn-clear">
            Clear
        </Button>
          <Button variant="link" className="btn-comments" onClick={handleShowComment}>
            Comments
        </Button>
      </ButtonGroup>
    </div>
  );
}

export default FilterContainer;
