import React from 'react';
import { NavLink, Link } from "react-router-dom";
import { BiChevronLeft } from 'react-icons/bi'
import './pageHeader.scss';


const PageHeader = (props) => {
  const { title, color, innerPage, breadcrumbs, handleActivePage, previousPage, handleBreadcrubsPreviousLink } = props;
  return (
    <div className={`page-header ${color}`}>
      <div className="breadcrumbs">
        <Link to={previousPage}>
          <BiChevronLeft viewBox="7 6 10 12"/>
        </Link>
        <ul>
          {breadcrumbs && breadcrumbs.map((item, index) => {
            return (
              <li key={index} className="item">
                {item.path 
                  ? <Link to={item.path} onClick={() => handleBreadcrubsPreviousLink()}>
                      {item.label}
                    </Link>
                  : <>{item.label}</>
                }
              </li>
            )
          })}
        </ul>
      </div>
      <div className="title">
        <h2>{title}</h2>
      </div>
      <div className="inner-page">
        <ul>
          {innerPage && innerPage.map((item, index) => {
            let currentPath = item.path.substring(item.path.lastIndexOf('/') + 1)
            return (
              <li key={index} className="item">
                <NavLink to={item.path} onClick={() => handleActivePage(currentPath)}>
                  {item.label}
                </NavLink>
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  );
}

export default PageHeader;
