import React, { useState } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Metro from './Metro';
import Customer from './Customer';

import './customerRevenue.scss';


const CustomerRevenue = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  const breadcrumbs = [
    {
      path: '/executive-cockpit/topline',
      label: 'EXECUTIVE COCKPIT'
    },
    {
      path: path === '/executive-cockpit/customer-revenue/overview' ? null : '/executive-cockpit/customer-revenue/overview',
      label: 'Revenue Detailed Page'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/executive-cockpit/customer-revenue/overview',
      label: 'Overview'
    },
    {
      path: '/executive-cockpit/customer-revenue/growth',
      label: 'Growth'
    },
    {
      path: '/executive-cockpit/customer-revenue/metro',
      label: 'Metro'
    },
    {
      path: '/executive-cockpit/customer-revenue/customer',
      label: 'Customer'
    }
  ]

  return (
    <div className="customer-revenue-page">
      <PageHeader
        title="CUSTOMER REVENUE DEEP-DIVE"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/executive-cockpit/topline"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'metro' && <Metro />}
        {currentPath === 'customer' && <Customer />}
      </div>
    </div>
  );
}

export default CustomerRevenue;
