import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { BsArrowDownRight, BsArrowUpRight } from "react-icons/bs";
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

// filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';


import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';

const Growth = () => {
    const [growthData, setData] = useState(null);
    const [isLoading, setLoadingStatus] = useState(true);
    const [resultData, setResultData] = useState(null);
    const [selectedData, setSelectedData] = useState(null);
    const [selectedBubbleData, setSelectedBubbleData] = useState(null);
    var [idState, setReferenceID] = useState(null);
    var [bubbleName, setBubbleName] = useState(null);

    const fetchData = async () => {
        setLoadingStatus(true);

        await Promise.all([axios(`${API_BASE_URL}/total-revenue/growth.json?print=pretty`), axios(`${API_BASE_URL}/total-revenue/growth/segment_ranking.json?print=pretty`)]
        ).then(response => {            
            const result = response[0].data ;
            const resultSelected = response[1];

            setData(result);
            setLoadingStatus(false);
            setResultData(resultSelected);
        }).catch(error => {
            console.log(error);
            setLoadingStatus(false);
        });;
        
    };

    useEffect(() => {
        let isMounted = true;

        if (isMounted) {
            fetchData();
        }

        return () => {
            isMounted = false;
        };
    }, []);

    var by_rank_id;
    const cardRef = useRef();
    const { width } = useWidowResize(cardRef);

    if (isLoading) return <div ref={cardRef}><Loader /></div>;

    if (resultData) {
        by_rank_id = resultData.data.by_rank_id;
    }

    if (idState) {
        selectedBubbleData.map(item => {
            if (item._id !== idState) {
                item.color = 'rgba(234, 240, 244, 0.2)';
            }
        })
    }

    return (
        <Container className="Growth" fluid ref={cardRef}>
            <FilterContainer title="Topline">
                <CheckboxDD
                    title="Region"
                    id="measeure"
                    options={optionRegion}
                />
                <SearchCheckboxDD
                    title="Metro"
                    id="metro"
                    options={optionMetro}
                />
                <RadioDD
                    title="Measure"
                    id="measeure"
                    options={optionMeasure}
                />
                <Separator height='39px' />
                <ResponsiveRadioDD
                    title="Range by"
                    id="range2"
                    wide={width >= 1394 ? true : false}
                    narrow={width < 1394 ? true : false}
                    options={optionRange}
                />
                <Separator height='39px' />
                <ResponsiveRadioDD
                    title="Group by"
                    id="group"
                    addTitleOnWide
                    wide={width >= 1516 ? true : false}
                    narrow={width < 1516 ? true : false}
                    options={optionGroup}
                />
            </FilterContainer>
            <GrowthContent
                bubbleName={bubbleName}
                selectedData={selectedData}
                idState={idState}
                width={width}
                resultData={resultData}
                selectedBubbleData={selectedBubbleData}
                setSelectedData={setSelectedData}
                setSelectedBubbleData={setSelectedBubbleData}
                setBubbleName={setBubbleName}
                setReferenceID={setReferenceID}
                growthData={growthData}
            />
        </Container>
    );
};

export default Growth;
