import React, { useRef } from 'react';
import { useWidowResize } from '../../constants/utilityHooks';
import BasicDD from '../../components/customDropdown/BasicDD'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveBasicDD from '../../components/customDropdown/ResponsiveBasicDD';
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import { optionMeasure, optionHyperscaler, optionRange, optionGroup, optionRegion, optionMetro } from '../../constants/filterOptions';
import Loader from '../../components/loader/Loader';

import './home.scss';

const Home = () => {
  const cardRef = useRef();
  const { width } = useWidowResize(cardRef);

  return (
    <div className="home-page" ref={cardRef}>
      <h1>Home page</h1>
      <p>Landing page</p>

      <h3 className="mb-3">Sample dropdown</h3>

      <section className="mb-5">
        <h5>Basic Dropdown</h5>
        <BasicDD
          title="Measeure"
          id="measeure"
          options={optionMeasure}
        />
        <br />
        <br />
        <p>try to resize the page</p>
        <ResponsiveBasicDD
          id="hyperscaler"
          wide={width >= 283 ? true : false}
          narrow={width < 283 ? true : false}
          options={optionHyperscaler}
        />
      </section>

      <section className="mb-5">
        <h5>Radio Dropdown</h5>
        <RadioDD
          id="range1"
          title="Range by"
          options={optionRange}
        />
        <br />
        <br />
        <p>try to resize the page</p>
        <ResponsiveRadioDD
          title="Range by"
          id="range2"
          wide={width >= 503 ? true : false}
          narrow={width < 503 ? true : false}
          options={optionRange}
        />
        <br />
        <br />
        <ResponsiveRadioDD
          title="Group by"
          id="group"
          wide={width >= 288 ? true : false}
          narrow={width < 288 ? true : false}
          options={optionGroup}
        />
      </section>

      <section className="mb-5">
        <h5>Check Dropdown</h5>
        <CheckboxDD
          title="Measeure"
          id="measeure"
          options={optionRegion}
        />
        <br />
        <br />
        <SearchCheckboxDD
          title="Metro"
          id="metro"
          options={optionMetro}
        />
      </section>
      <Loader withContainer width='50px' />
    </div>
  )
};

export default Home;
