export default (state, action) => {
  switch (action.type) {
      case "LOGIN":
          localStorage.setItem("token", JSON.stringify(action.payload.token));
          localStorage.setItem("full_name", JSON.stringify(action.payload.user.full_name));
          localStorage.setItem("email_id", JSON.stringify(action.payload.user.email_id));
          return {
              ...state,
              isAuthenticated: true,
              token: action.payload.token,
              user: {
                  full_name: action.payload.user.full_name,
                  email_id: action.payload.user.email_id
              }
          };
      case "LOGOUT":
          localStorage.clear();
          return {
              ...state,
              isAuthenticated: false,
          };
      default: return state;
  }
}