import React from 'react';
import CustomTable from '../../components/customTable/CustomTable';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import './executiveCockpit.scss';


const Content = ({ data }) => {

  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
         <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
         <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{ rowIndex+1 }</span>
    );
  }
  // For Ranks ===

  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    if (isSelect) {
      alert(
        `im selected! I'm row-index ${rowIndex+1} \n` +
        `here is my data: ${JSON.stringify(row)}`
      )
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField:'segment',
      order:'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      sort: true,
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'revenue',
      text: 'REVENUE',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center' 
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center', 
      headerAlign: 'center' 
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'center', 
      headerAlign: 'center' 
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q/Q GROWTH%',
      sort: true,
      align: 'center', 
      headerAlign: 'center' 
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: indicatorDown,
      align: 'center', 
      headerAlign: 'center' 
    },
    {
      dataField: 'percent_budget',
      text: '%BUDGET',
      sort: true,
      align: 'center', 
      headerAlign: 'center' 
    },
    {
      dataField: 'percent_mpq',
      text: '%MPQ',
      sort: true,
      align: 'center', 
      headerAlign: 'center' 
    }
  ]

  return (
    <div className="topline-page">
      <h1>Topline</h1>
      <ul>
        <li><a href="#/executive-cockpit/total-revenue/overview">Total Revenue - Overview</a></li>
        <li><a href="#/executive-cockpit/total-revenue/growth">Total Revenue - Growth</a></li>
        <li><a href="#/executive-cockpit/total-revenue/metro">Total Revenue - Metro</a></li>
        <li><a href="#/executive-cockpit/customer-revenue/overview">Customer Revenue - Overview</a></li>
        <li><a href="#/executive-cockpit/customer-revenue/growth">Customer Revenue - Growth</a></li>
        <li><a href="#/executive-cockpit/customer-revenue/metro">Customer Revenue - Metro</a></li>
        <li><a href="#/executive-cockpit/customer-revenue/customer">Customer Revenue - Customer</a></li>
      </ul>


      <CustomTable
        tableName='demo-table'
        data={data === undefined ? [] : data}
        selectRow={selectRow} // For Selecting Row ===
        defaultSorted={defaultSorted} // For Initial Sort ===
        rowClasses={rowClasses}
        columns={columns}
      />
    </div>
  )
};

export default Content;


