import React, { useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi';
import './customDropdown.scss';



const ResponsiveRadioDD = ({ title, options, wide, narrow, className, id, addTitleOnWide, visibleInModal }) => {
  const initialValue = options ? options[0].text : 'try adding something to click to, use "option" props.'
  const [value, setValue] = useState(initialValue);
  const [show, setShow] = useState(false);

  const handleSelect = (e) => {
    setValue(e)
  }

  const handleToggle = () => {
    setShow(!show)
  }

  const handleCancel = () => {
    setShow(!show)
  }

  return (
    <>
      {visibleInModal 
        ? <div className="radio-in-modal narrow-list filter-modal">
            <div className="title">{title ? title : value}</div>
            <Form>
              {options && options.map(option => (
                <Form.Check
                  key={option.key}
                  custom
                  type='radio'
                  id={`modal_radio_${option.key}`}
                  checked={value === option.text}
                  label={option.text}
                  onChange={() => handleSelect(option.text)}
                />
              ))}
            </Form>
          </div>
        : <>
          {wide &&
            <ul className={`responsive-radio-dd wide-list ${wide ? 'visible' : 'hidden'} ${className ? className : ''}`}>
              {options
                ? <>
                  {options.map(option => (
                    <li key={option.key}
                      onClick={() => handleSelect(option.text)}
                      className={`${option.text === value ? 'active' : ''}`}
                    >
                      {addTitleOnWide ? `${title ? title : ''} ` : ''}{option.text}
                    </li>
                  ))}
                  </>
                : <li>{initialValue}</li> 
              }
            </ul>
          }
    
          {narrow && 
            <Dropdown
              show={show}
              onToggle={handleToggle}
              className={`responsive-radio-dd narrow-list ${narrow ? 'visible' : 'hidden'} ${className ? className : ''}`}
            >
              <Dropdown.Toggle id={`responsive_radio_dd-${id}`} variant='light'>
                {title ? title : value} <BiChevronDown viewBox="6 8 12 12" />
              </Dropdown.Toggle>
    
              <Dropdown.Menu show={show}>
                {options && options.map(option => (
                  <Form.Check
                    key={option.key}
                    custom
                    type='radio'
                    id={`raido_res_${option.key}`}
                    checked={value === option.text}
                    label={option.text}
                    onChange={() => handleSelect(option.text)}
                  />
                ))}
                <div className="btn-block">
                  <Button variant="link" onClick={handleCancel}>Cancel</Button>
                  <Button variant="primary">Apply</Button>
                </div>
              </Dropdown.Menu>
            </Dropdown>
          }
        </>
      }
    </>
  );
}

export default ResponsiveRadioDD;
