import React from 'react';
import { ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col, Table } from 'react-bootstrap';
import SparklineCustom from '../../components/chart/SparklineCustom';
import MapChart from '../../components/chart/MapChart';
import DonutChart from '../../components/chart/DonutChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import sampleFlagImage from '../../assets/images/sample-flag.png';

import './customerRevenue.scss';
import { over } from 'lodash';

const CustomerModal = (props) => {
  const data = props.data;
  var overall,
    vertical_details,
    vertical_revenue_mix,
    metro, deployment_map;
  if (data) {
    overall = data.overall;
    vertical_details = data.vertical_details;
    vertical_revenue_mix = data.vertical_revenue_mix;
    metro = data.metro;
    deployment_map = data.deployment_map
  }
  return (
    <Modal id="customer-modal" className="customer-modal"
      {...props}
      // size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {metro} total revenue
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col className="fix-width" style={{ paddingRight: "30px" }}>
                <Kpi data={overall} />
              </Col>
              <Col md={6} xs={12}>
                <Legend legend={deployment_map.legend} />
                <div style={{ height: "350px ", paddingTop: "2rem" }}>
                  <MapChart
                    id={"customer_revenue_map_chart_modal"}
                    largerWidth={false}
                    data={deployment_map.data}
                  />
                </div>
              </Col>
              <Col style={{ paddingLeft: "30px" }}>
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"customer_revenue_metro_modal_donut_chart"}
                    totalTitle={"TOTAL REVENUE"}
                    total={vertical_revenue_mix.customer_revenue}
                    data={vertical_revenue_mix.data}
                    legend={vertical_revenue_mix.legend} />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <div className="scrollable-customer-modal">
                  <Table responsive >
                    <thead>
                      <tr>
                        <th className="header-align-left">rank</th>
                        <th className="header-align-left">metro</th>
                        <th className="header-align-center">mrr</th>
                        <th className="header-align-center"> mrr MIX%</th>
                        <th />
                        <th className="header-align-center">Y/Y GROWTH%</th>
                        <th className="header-align-center">Y-Y $ GROWTH</th>
                        <th className="header-align-center">%customer total</th>
                        <th className="header-align-center">%metro total</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        vertical_details && vertical_details.length > 0 ?
                          vertical_details.map((data, i) => {
                            return <tr key={i}  className="height1">
                              <td className="text-left">{i + 1}</td>
                              <td className="body-info-column text-left">
                                <img className="flag-icon" src={sampleFlagImage} alt="sample flag" />
                                <span className="body-name">{data.metro}</span>
                              </td>
                              <td className="text-center">{data.mrr}</td>
                              <td className="text-center">{data.mrr_mix_percent}</td>
                              <td className="sparkline-td">
                                <SparklineCustom />
                              </td>
                              <td className="text-center">
                                <span className="indicator indicator-down"> <ImArrowDownRight2 /> {data.y_by_y_growth_percent}%</span>
                              </td>
                              <td className="text-center">{data.y_to_y_usd_growth}</td>
                              <td className="text-center">{data.percent_customer_total}</td>
                              <td className="text-center">{data.percent_metro_total}</td>
                            </tr>
                          }) : null
                      }

                    </tbody>
                  </Table>
                </div>

              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default CustomerModal;