import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap';
import Authenticated from '../../assets/images/phone-correct.png';
import FooterLogo from '../../assets/images/ping-id.png';

import './fakeLogin.scss';

const FakeLogin = () => {
    let history = useHistory();

    const [sms_code, setCode] = useState('');
    const [appear, setAppear] = useState(false);
    const [openSms, setOpen] = useState(false);

    const handleSubmit = () => {
        setAppear(true)
        setTimeout(()=> {
            history.push("/");
        }, 2000)
    }

    const goToSms = () => {
        setOpen(true);
    }

    return (
        <>
        {openSms
        ?   <FakeSMS 
                appear={appear}
                handleSubmit={handleSubmit}
                sms_code={sms_code}
                setCode={setCode}
            />
        :   <div className="fake-login">
                <div className="container-area">
                    <h4>Sign On</h4>
                    <Form>
                        <Form.Group controlId="username" className="mb-4">
                            <Form.Label>Username</Form.Label>
                            <Form.Control type="text" placeholder="Username" name="username" required />
                        </Form.Group>
                        <Form.Group controlId="password">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" placeholder="Password" name="password" required />
                        </Form.Group>
                        <div className="btn-block">
                            <Button type="submit" variant="info" onClick={() => goToSms()}>Sign On</Button>
                        </div>
                    </Form>
                </div>

                <div className="footer-area">
                    <p className="powered">Powered by PingIdentity</p>
                    <p className="copy">Copyright &copy; 2003-2019. Ping Identity Corporation. All rights reserved.</p>
                </div>
            </div>
        }
        </> 
    );
};

const FakeSMS = ({ appear, handleSubmit, sms_code, setCode }) => {
    return (
        <div className="fake-sms">
            <div className="container-area">
                {appear 
                ?   <div className="container-wrapper green">
                        <p><img src={Authenticated} alt=""/></p>
                        <h4>Authenticated</h4>
                    </div>
                :   <div className="container-wrapper">
                        <h6>Authentication</h6>
                        <p className="text-bold">SMS sent to Mobile 1(********91)</p>
                        <p className="mb-5">Enter the passcode you received.</p>

                        <Form onSubmit={handleSubmit}>
                            <Form.Control type="text" name="sms_code" value={sms_code} onChange={(e) => setCode(e.target.value)}/>
                            <Button variant="resend">Resend Passcode</Button>
                            <div className="btn-block">
                                <Button variant="outline-light">Change Device</Button>
                                <Button variant="outline-light" 
                                    disabled={sms_code === '' ? true : false } 
                                    onClick={()=> handleSubmit()}
                                >
                                    Sign On
                                </Button>
                            </div>
                        </Form>
                    </div>
                }
                <p className="help">Please contact help@equinix.com</p>
                <Button variant="outline-secondary">Settings</Button>
            </div>

            <div className="footer-area">
                <p className="logo"><img src={FooterLogo} alt=""/></p>
                <p className="copy">Copyright &copy; 2003-2019. Ping Identity Corporation. All rights reserved.</p>
            </div>
        </div>
    );
}

export default FakeLogin;
